<template>
  <div class="add">
    <h2 style="margin-left:1em"></h2>
    <Divider dashed />

    <Form
      ref="formItem"
      :model="formItem"
      :label-width="50"
      label-position="left"
      :rules="ruleValidate"
    >
      <FormItem label="" prop="title">
        <Input v-model="formItem.title" placeholder="..."></Input>
      </FormItem>
      <FormItem label="" prop="url">
        <Input v-model="formItem.url" placeholder="..."></Input>
      </FormItem>
      <FormItem label="icon" prop="cover">
        <Input v-model="formItem.cover" placeholder="icon"></Input>
      </FormItem>
      <FormItem label="" prop="category">
        <Select v-model="formItem.category">
          <Option value="beijing">New York</Option>
          <Option value="shanghai">London</Option>
          <Option value="shenzhen">Sydney</Option>
        </Select>
      </FormItem>

      <FormItem label="E-mail" prop="email">
        <Input v-model="formItem.email" placeholder="()"></Input>
      </FormItem>

      <FormItem label="" prop="description">
        <Input
          v-model="formItem.description"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder=""
        ></Input>
      </FormItem>

      <FormItem label="">
        <Input
          v-model="formItem.note"
          type="textarea"
          :autosize="{minRows: 2,maxRows: 5}"
          placeholder=""
        ></Input>
      </FormItem>

      <FormItem>
        <Button id="btn1" @click="handleSubmit('formItem')" type="primary"></Button>
        <Button id="btn2" @click="handleReset" style="margin-left: 8px"></Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      formItem: {
        title: "",
        url: "",
        category: "",
        email: "",
        description: "",
        cover: "",
        note: ""
      },

      ruleValidate: {
        title: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        url: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        category: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        description: [
          {
            required: false
          },
          {
            type: "string",
            max: 40,
            message: "40",
            trigger: "blur"
          }
        ]
      }
    };
  },
  methods: {
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          this.axios({
            method: "post",
            url: "/websites/add",
            data: this.formItem
          }).then(response => {
