          this.$Message.error("URL");
        }
      });
    },
    handleReset() {
      this.formData = {
        title: "",
        url: "",
        category: "",
        email: "",
        description: ""
      };
    }
  }
};
</script>

<style scoped>
.add {
  padding: 1em;
  margin: 100px auto;
  max-width: 500px;
}
</style>




