<template>
  <div id="website">
    <div id="option" style="margin:1em">
      <Input
        class="search"
        placeholder="..."
        style="width: 300px"
        v-model="keywords"
        v-on:input="searchByWords"
      />
      <!-- <Button id="btn1" type="primary" @click="searchByWords"></Button> -->

      <router-link to="/addweb">
        <Button id="btn2"></Button>
      </router-link>

      <div @mouseenter="show = true" @mouseleave="show = false" class="tip">
        <Icon type="md-ionic" />
      </div>
    </div>

    <transition name="slide-fade">
      <div class="tip-box" v-if="show">
        <p>
          (...)
          <a
            href="http//images.kkboom.cn"
            target="_blank"
          >http//images.kkboom.cn</a>apiPS
          <a href>Pixabay</a>
        </p>
      </div>
    </transition>

    <Divider dashed />
    <Row class="code-row-bg" justify="start" type="flex">
      <Col :key="index" :lg="6" :md="12" :sm="24" :xs="24" v-for="(item,index) in webForRow">
        <Cardweb :index="index" :website="item"></Cardweb>
      </Col>
    </Row>
    <!--  -->
    <div v-if="!webForRow.length">
      <h4></h4>
    </div>
  </div>
</template>

<script>
import Cardweb from './Card-web'

export default {
    data() {
        return {
            webForRow: [],
            keywords: '',
            tempRow: [],
            show: false
        }
    },
    components: { Cardweb },
    methods: {
        searchByWords() {
            // 
            var re = /\S+/
            if (re.test(this.keywords)) {
                this.axios
                    .get('/websites/search?k=' + this.keywords)
                    .then(res => {
                        if (res.status == 200) {
                            // 
                            if (res.data.webs) {
                                const item = res.data.webs
                                this.webForRow = item
                            } else {
                                this.webForRow = []
                            }
                        } else {
                            this.$Notice.error({
                                title: '',
                                desc: ''
                            })
                        }
                    })
            } else {
                this.webForRow = this.tempRow
            }
        }
    },
    created() {
        // web
        this.axios.get('/websites').then(res => {
            const webs = res.data.webs
            this.webForRow = webs
            // 
            this.tempRow = webs
        })

        // 
        this.axios
            .get('http://kkboom.cn:9876/images?num=' + this.webForRow.length)
            .then(res => {
                const img_list = res.data.img_list
                const imgStr = []
                img_list.forEach(item => {
                    imgStr.push(item.url)
                })
                window.localStorage.setItem('imgStr', imgStr)
            })
    }
}
</script>

<style scoped>
#website {
    width: 1080px;
    margin: 80px auto;
}

#btn2 {
    background-color: rgb(175, 50, 60);
    color: #f9f7f4;
}
#btn1,
#btn2 {
    margin-left: 10px;
    border: 1px solid rgb(175, 50, 60);
}

.tip {
    float: right;
    font-size: 1.6em;
}
.tip:hover {
    color: #9aa899;
    cursor: pointer;
}
.tip i {
    margin-top: -3px;
}

.tip-box {
    background-color: #f9f7f4;
    margin: 1em;
    padding: 1em;
    font-size: 1.6em;
    box-shadow: 0 0 40px #222;
}

.tip-box a {
    color: #ff6b6b;
}

.slide-fade-enter-active {
    transition: all 0.3s ease;
}
.slide-fade-leave-active {
    transition: all 0.8s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */ {
    transform: translateX(10px);
    opacity: 0;
}
</style>



