<template>
  <Form id="login">
    <FormItem label="">
      <Input v-model="mail" placeholder="..."></Input>
    </FormItem>
    <FormItem label="" prop="password">
      <Input type="password" v-model="password" placeholder="..."></Input>
    </FormItem>
    <FormItem>
      <Button type="primary" @click="handleSubmit"></Button>
      <Button @click="handleReset" style="margin-left: 8px"></Button>
    </FormItem>
  </Form>
</template>
<script>
// import Vue from 'vue'
export default {
  data() {
    return {
      mail: "",
      password: ""
    };
  },
  methods: {
    handleSubmit() {
      if (!this.mail || !this.password) {
        this.$Message.warning("");
      } else {
        this.axios
          .post("/login", {
            email: this.mail,
            password: this.password
          })
          .then(response => {
            if (response.data.user) {
              this.$Message.success("!");
              // busheader
              window.localStorage.setItem("user", response.data.user.name);
              // tokenlocalstorage
              window.localStorage.setItem("token", response.data.token);
              this.$router.push({ path: "/" });
            } else {
              this.$Message.error(response.data.msg);
            }
          });
      }
    },
    // 
    handleReset() {
      this.password = "";
      this.mail = "";
    }
  }
};
</script>

<style>
#login {
  max-width: 360px;
  padding: 1em;
  margin: 100px auto;
  top: 200px;
  bottom: 0;
  left: 0;
  right: 0;
}
</style>




