<template>
  <div class="container">
    <Divider orientation="right" style="color:#fff"></Divider>
    <Form label-position="top">
      <div id="form_lable">
        <label></label>
        <FormItem>
          <Input v-model="formData.title"></Input>
        </FormItem>
        <label for>Mavon-EditorMarkdown</label>
        <!-- markdown -->
        <mavon-editor
          :toolbars="markdownOption"
          @imgAdd="$imgAdd"
          @imgDel="$imgDel"
          class="markdown"
          ref="md"
          v-model="formData.content"
        />

        <label for></label>
        <FormItem>
          <Input
            :autosize="{minRows: 2,maxRows: 5}"
            placeholder="Enter something..."
            type="textarea"
            v-model="formData.summary"
          ></Input>
        </FormItem>
        <label for></label>
      </div>
      <FormItem>
        <Input placeholder="" type="text" v-model="formData.category"></Input>
      </FormItem>
      <!--  -->
      <FormItem>
        <Button @click="handleSubmit('formData')" id="btn1" type="primary"></Button>
        <Button @click="handleReset" id="btn2" style="margin-left: 8px"></Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
export default {
    data() {
        return {
            // 
            formData: {
                summary: '',
                title: '',
                content: '',
                categories: [],
                category_add: ''
            },
            img_file: {},
            markdownOption: {
                fontSize: '14px',
                bold: true, // 
                italic: true, // 
                header: true, // 
                underline: true, // 
                strikethrough: true, // 
                mark: true, // 
                superscript: true, // 
                subscript: true, // 
                quote: true, // 
                ol: true, // 
                ul: true, // 
                link: true, // 
                imagelink: true, // 
                code: true, // code
                table: true, // 
                // fullscreen: true, // 
                readmodel: true, // 
                htmlcode: true, // html
                help: true, // 
                /* 1.3.5 */
                undo: true, // 
                redo: true, // 
                trash: true, // 
                save: true, // eventssave
                /* 1.4.2 */
                navigation: true, // 
                /* 2.1.8 */
                alignleft: true, // 
                aligncenter: true, // 
                alignright: true, // 
                /* 2.2.1 */
                subfield: true, // 
                preview: true // 
            }
        }
    },
    methods: {
        // 
        // 
        $imgAdd(pos, $file) {
            // 
            this.img_file[pos] = $file
        },
        // 
        $imgDel(pos) {
            delete this.img_file[pos]
        },

        // 
        handleSubmit(name) {
            // ..
            var formdata = new FormData()
            for (var _img in this.img_file) {
                formdata.append(_img, this.img_file[_img])
            }

            // 
            if (formdata) {
                this.axios({
                    url: '/upload/img',
                    method: 'post',
                    data: formdata,
                    headers: {
                        'Content-Type': 'multipart/form-data',
                        Authorization: localStorage.getItem('token')
                    }
                }).then(res => {
                    if (res.status == 200) {
                        var url_list = res.data.urls
                        /**
                         *  res = [[pos, url], [pos, url]...]
                         * pos 0
                         * url url
                         */
                        // .url![...](0) -> ![...](url)
                        for (var i = 0; i < url_list.length; i++) {
                            // $vm.$img2Url 
                            // console.log(url_list[i]);
                            this.$refs.md.$img2Url(
                                parseInt(url_list[i].key),
                                url_list[i].url
                            )
                        }
                        // 
                        //
                        this.axios({
                            method: 'post',
                            url: '/blogs/add',
                            data: this.formData,
                            headers: {
                                Authorization: localStorage.getItem('token')
                            }
                        }).then(response => {
                            this.$Notice.success({
                                title: ''
                            })
                        })
                    } else {
                        nodesc = res.data.msg
                        this.$Notice.error({
                            title: '',
                            desc: nodesc ? '' : ''
                        })
                    }
                })
            }
        },

        handleReset() {
            this.formData = {
                summary: '',
                title: '',
                content: '',
                categories: [],
                category_add: ''
            }
        }
    }
}
</script>


<style scoped>
#tip {
    text-align: left;
    margin: 0 0 10px;
}

#form_lable label {
    padding-left: 20px;
    text-align: left;
    display: block;
    margin-left: 0;
    margin-bottom: 16px;
    color: beige;
    /* float: left; */
    border-left: 4px solid rgb(175, 50, 60);
}
#form_lable .markdown {
    border-radius: 5px;
    margin-bottom: 20px;
    height: 500px;
    max-height: 500px;
}
#form_lable .v-note-wrapper {
    z-index: 0;
}
.container {
    max-width: 1080px;
    margin: 0 auto;
    padding-top: 80px;
}
</style>



